import {Component, Input, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {AtmService} from '../atm.service';
import {Account, Customer} from '../entity';
import {Location} from '@angular/common';

@Component({
  selector: 'app-changepw',
  templateUrl: './changepw.component.html',
  styleUrls: ['./changepw.component.css']
})
export class ChangepwComponent implements OnInit {
  @Input() account: Account;
  customers: Customer[];
  constructor(
    private route: ActivatedRoute,
    public router: Router,
    private changePwService: AtmService,
    private location: Location
  ) { }

  ngOnInit() {
    this.getCustomers();
    const id = this.route.snapshot.params.id;
    this.changePwService.getAccount(id)
      .subscribe( response => this.account = response);
  }
  compareFn(o1: Customer, o2: Customer): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }
  goBack(): void {
    this.location.back();
  }
  getCustomers(): void {
    this.changePwService.getCustomers()
      .subscribe(res => this.customers = res);
  }
  save( newpw1: string, newpw2: string): void {
    if (newpw1 === newpw2) {
      this.account.passwd = newpw2;
      this.changePwService.changePw(this.account)
        .subscribe((response) => {
          alert(response.message);
          this.goBack();
        });
    } else {
      alert('密码不一致');
    }
  }
}
