import {Component, Input, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {AtmService} from '../atm.service';
import {Account} from '../entity';
import {Location} from '@angular/common';

@Component({
  selector: 'app-savemoney',
  templateUrl: './savemoney.component.html',
  styleUrls: ['./savemoney.component.css']
})
export class SavemoneyComponent implements OnInit {
  @Input() account: Account;
  deal: { acctId: number, amount: number };

  constructor(private route: ActivatedRoute,
              private location: Location,
              private saveService: AtmService
  ) {
  }

  ngOnInit() {
    const id = this.route.snapshot.params.id;
    this.saveService.getAccount(id)
      .subscribe(response => this.account = response);
  }

  goBack(): void {
    this.location.back();
  }


  save(amount: number): void {
    this.deal = {acctId: this.account.id, amount};
    if (!amount) {
      return;
    }
    this.saveService.saveMoney(this.deal)
      .subscribe((response) => {
        alert(response.message);
        this.goBack();
      });
  }

  Number(value: string) {
    return Number(value);
  }
}
