import {Component, Input, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {Location} from '@angular/common';
import {AtmService} from '../atm.service';
import {Account} from '../entity';

@Component({
  selector: 'app-transfer',
  templateUrl: './transfer.component.html',
  styleUrls: ['./transfer.component.css']
})
export class TransferComponent implements OnInit {
  @Input() account: Account;
  deal: { outAcct: string, inAcct: string, amount: number };

  constructor(private route: ActivatedRoute,
              private location: Location,
              private transferService: AtmService
  ) {
  }

  ngOnInit() {
    const id = this.route.snapshot.params.id;
    this.transferService.getAccount(id)
      .subscribe(response => this.account = response);
  }

  save(inAcct: string, amount: number): void {
    this.deal = {outAcct: this.account.acct, inAcct, amount};
    if (!inAcct) {
      return;
    }
    this.transferService.transferMoney(this.deal)
      .subscribe((response) => {
        alert(response.message);
        this.goBack();
      });
  }

  goBack(): void {
    this.location.back();
  }

  Number(value: string) {
    return Number(value);
  }
}
