import { Injectable } from '@angular/core';
import {Account, Customer} from './entity';

@Injectable({
  providedIn: 'root'
})
export class AcctService {
  public account: Account;
  public customer: Customer;
  public acctId: number;
  public roleId: number;
  setCustomer(customer: Customer) {
    this.customer = customer;
  }
  getCustomer() {
    return this.customer;
  }
  setAccount(account: Account) {
    this.account = account;
  }
  getAccount() {
    return this.account;
  }
  setAcctId(acctId: number) {
    this.acctId = acctId;
  }
  getAcctId() {
    return this.acctId;
  }
  setAcctRoleId(acctRoleId: number) {
    this.roleId = acctRoleId;
  }
  getAcctRoleId() {
    return this.roleId;
  }
  constructor() { }
}
