import {Component, OnInit} from '@angular/core';
import {Account, Deal} from '../entity';
import {AtmService} from '../atm.service';

@Component({
  selector: 'app-admdeal',
  templateUrl: './admdeal.component.html',
  styleUrls: ['./admdeal.component.css']
})
export class AdmdealComponent implements OnInit {
  private deals: Deal[];
  private accountToCheck: Account;

  constructor(private dealService: AtmService) {
  }

  ngOnInit() {
    this.getDeals();
  }

  getDeals(): void {
    this.dealService.getDeals()
      .subscribe(response => this.deals = response);
  }

  getDealsAccurately(beginTime: string, endTime: string): void {
    this.dealService.getDealsAccurately(beginTime, endTime).subscribe(res => this.deals = res);
  }

  getAcctDealsAccurately(acct: string, beginTime: string, endTime: string): void {
    if (acct.trim().length !== 0) {
      this.dealService.getAccountByAcct(acct).subscribe(res => this.accountToCheck = res);
      if (this.accountToCheck != null) {
        this.dealService.getAcctDealsAccurately(this.accountToCheck.id, beginTime, endTime)
          .subscribe(res => {
            this.deals = res;
          });
      } else {
        this.deals = null;
      }
    } else {
      this.getDealsAccurately(beginTime, endTime);
    }
  }
}
