import {Component, OnInit} from '@angular/core';
import {AcctService} from '../acct.service';
import {Router} from '@angular/router';
import {AtmService} from '../atm.service';
import {HostBinding } from '@angular/core';
import {Account} from '../entity';
import {slideInDownAnimation} from '../animations';

@Component({
  animations: [slideInDownAnimation],
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.css']
})
export class IndexComponent implements OnInit {
  // accountToSave: Account;
  // accounts: Account[];
  private acctId: number;
  private customerName: string;
  acctToSave = window.localStorage.getItem('acctId');
  account = window.localStorage.getItem('customerName');
  @HostBinding('@routeAnimation') routeAnimation = true;
  constructor(private IndexService: AtmService, private acctService: AcctService, public router: Router, private atmService: AtmService) {
  }

  logout() {
    this.router.navigate(['']);
    this.atmService.logOut().subscribe(response => alert(response.message));
  }
  ngOnInit() {
    this.getCustomerName();
    this.getAcctId();
  }

  getCustomerName() {
    this.customerName = this.acctService.getCustomer().name;
    // this.customerName =  '危怡凯';
  }

  getAcctId() {
    this.acctId = this.acctService.getAcctId();
  }
}
