import {Component, Input, OnInit} from '@angular/core';
import {Account, Customer, Role} from '../entity';
import {ActivatedRoute} from '@angular/router';
import {AtmService} from '../atm.service';
import {Location} from '@angular/common';

@Component({
  selector: 'app-update-account',
  templateUrl: './update-account.component.html',
  styleUrls: ['./update-account.component.css']
})
export class UpdateAccountComponent implements OnInit {
  @Input() customers: Customer[];
  roles: Role[];
  account: Account;


  constructor(
    private route: ActivatedRoute,
    private accountService: AtmService,
    private location: Location
  ) { }

  ngOnInit() {
    this.getCustomers();
    this.getRoles();
    const id = this.route.snapshot.params.id;
    this.accountService.getAccount(id)
      .subscribe(res => {
        this.account = res;
      });
  }

  compareFn(o1: Customer, o2: Customer): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }
  compareFn1(o1: Role, o2: Role): boolean {
    return o1 && o2 ? o1.id === o2.id : o1 === o2;
  }

  getCustomers(): void {
    this.accountService.getCustomers()
      .subscribe(res => this.customers = res);
  }
  getRoles(): void {
    this.accountService.getRoles()
      .subscribe(res => this.roles = res);
  }

  save(): void {
    this.accountService.updateAccount(this.account)
      .subscribe((res) => {
        alert(res.message);
        this.goBack();
      });
  }

  goBack(): void {
    this.location.back();
  }
}
