import { Component, OnInit } from '@angular/core';
import { Customer} from '../entity';
import {AtmService} from '../atm.service';

@Component({
  selector: 'app-customer',
  templateUrl: './customer.component.html',
  styleUrls: ['./customer.component.css']
})
export class CustomerComponent implements OnInit {
  customers: Customer[];
  constructor(private customerService: AtmService) { }

  ngOnInit() {
    this.getCustomers();
  }
  getCustomers(): void {
    this.customerService.getCustomers()
      .subscribe(response => this.customers = response);
  }
  delete(customer: Customer | number): void {
    this.customers = this.customers.filter(h => h !== customer);
    this.customerService.deleteCustomer(customer).subscribe(res => alert(res.message));
  }
}
