import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {RouterModule, Routes} from '@angular/router';
import {LoginComponent} from './login/login.component';
import {IndexComponent} from './index/index.component';
import {SavemoneyComponent} from './savemoney/savemoney.component';
import {DrawmoneyComponent} from './drawmoney/drawmoney.component';
import {CheckComponent} from './check/check.component';
import {TransferComponent} from './transfer/transfer.component';
import {ChangepwComponent} from './changepw/changepw.component';
import {LogoutComponent} from './login/logout.component';
import {WelcomeComponent} from './welcome/welcome.component';
import {AddAccountComponent} from './add-account/add-account.component';
import {UpdateAccountComponent} from './update-account/update-account.component';
import {AdmIndexComponent} from './adm-index/adm-index.component';
import {CustomerComponent} from './customer/customer.component';
import {AddCustomerComponent} from './add-customer/add-customer.component';
import {UpdateCustomerComponent} from './update-customer/update-customer.component';
import {DealComponent} from './deal/deal.component';
import {AdmdealComponent} from './admdeal/admdeal.component';
import {CheckbalanceComponent} from './checkbalance/checkbalance.component';

const routes: Routes = [
  {path: '', component: LoginComponent, pathMatch: 'full'},
  {path: 'logout', component: LogoutComponent},
  {path: 'index', component: IndexComponent, children: [
      {path: 'welcome', component: WelcomeComponent},
      {path: 'checkbalance/:id', component: CheckbalanceComponent},
      {path: 'savemoney/:id', component: SavemoneyComponent},
      {path: 'drawmoney/:id', component: DrawmoneyComponent},
      {path: 'transfer/:id', component: TransferComponent},
      {path: 'deals', component: DealComponent},
      {path: 'changepw/:id', component: ChangepwComponent}
    ]
  },
  {path: 'admindex', component: AdmIndexComponent, children: [
      {path: 'welcome', component: WelcomeComponent},
      {path: 'checkbalance/:id', component: CheckbalanceComponent},
      {path: 'savemoney/:id', component: SavemoneyComponent},
      {path: 'drawmoney/:id', component: DrawmoneyComponent},
      {path: 'check', component: CheckComponent},
      {path: 'deals', component: DealComponent},
      {path: 'admdeals', component: AdmdealComponent},
      {path: 'transfer/:id', component: TransferComponent},
      {path: 'changepw/:id', component: ChangepwComponent},
      {path: 'customers', component: CustomerComponent},
      {path: 'addCustomer', component: AddCustomerComponent},
      {path: 'updateCustomer/:id', component: UpdateCustomerComponent},
      {path: 'addAccount', component: AddAccountComponent},
      {path: 'updateAccount/:id', component: UpdateAccountComponent}
    ]
  }
];

@NgModule({
  declarations: [],
  imports: [
    CommonModule,
    RouterModule.forRoot(routes)
  ]
})
export class AppRoutingModule {
}
