import {Component, OnInit} from '@angular/core';
import {Account, Deal} from '../entity';
import {AtmService} from '../atm.service';
import {AcctService} from '../acct.service';

@Component({
  selector: 'app-deal',
  templateUrl: './deal.component.html',
  styleUrls: ['./deal.component.css']
})
export class DealComponent implements OnInit {
  private deals: Deal[];
  private currentAccount: Account;

  constructor(
    private dealService: AtmService,
    private customerService: AcctService,
  ) {
  }

  ngOnInit() {
    this.getCurrentAccount();
    this.getDealsByAcctId();
  }

  getDealsByAcctId(): void {
    this.dealService.getDealsByAcctId(this.customerService.getAcctId())
      .subscribe(res => {
          this.deals = res;
        }
      );
  }

  getCurrentAccount() {
    this.currentAccount = this.customerService.getAccount();
  }

  getAcctDealsAccurately(beginTime: string, endTime: string): void {
    this.dealService.getAcctDealsAccurately(this.currentAccount.id, beginTime, endTime)
      .subscribe(res => {
        this.deals = res;
      });
  }
}
