import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable, of} from 'rxjs';
import {Account, Customer, Deal, Response, Role} from './entity';
import {catchError} from 'rxjs/operators';

const httpOptions = {
  headers: new HttpHeaders({'Content-Type': 'application/json'})
};

@Injectable({
  providedIn: 'root'
})
export class AtmService {
  response: Response;
  private loginUrl = '../login';
  private accountUrl = '../account.ctl';
  private customerUrl = '../customer.ctl';
  private roleUrl = '../role.ctl';
  private saveMoneyUrl = '../save.ctl';
  private drawMoneyUrl = '../draw.ctl';
  private transferUrl = '../transfer.ctl';
  private dealUrl = '../deal.ctl';
  private changePwUrl = '../change.ctl';
  private logoutUrl = '../logout';

  constructor(private http: HttpClient) {
  }

  // 检查账户正确性
  checkAccount(account: { acct: string, passwd: string }): Observable<any> {
    return this.http.post<any>(this.loginUrl, account);
  }

  logOut(): Observable<any> {
    return this.http.get<any>(this.logoutUrl);
  }

  saveMoney(deal: { acctId: number, amount: number }): Observable<Response> {
    return this.http.get<Response>(this.saveMoneyUrl + '?acctId=' +
      deal.acctId + '&amount=' + deal.amount, httpOptions).pipe(
      catchError(this.handleError<Response>('saveMoney'))
    );
  }

  drawMoney(deal: { acctId: number, amount: number }): Observable<Response> {
    return this.http.get<Response>(this.drawMoneyUrl + '?acctId=' +
      deal.acctId + '&amount=' + deal.amount, httpOptions).pipe(
      catchError(this.handleError<Response>('saveMoney'))
    );
  }

  transferMoney(deal: { outAcct: string, inAcct: string, amount: number }): Observable<Response> {
    return this.http.get<Response>(this.transferUrl + '?outAcct='
      + deal.outAcct + '&inAcct=' + deal.inAcct + '&amount=' + deal.amount, httpOptions).pipe(
      catchError(this.handleError<any>('transferMoney'))
    );
  }

  getDeals(): Observable<Deal[]> {
    return this.http.get<Deal[]>(this.dealUrl).pipe(
      catchError(this.handleError<Deal[]>('getDeals', [])));
  }

  getDealsByAcctId(acctId: number): Observable<Deal[]> {
    const url = this.dealUrl + '?acctId=' + acctId;
    return this.http.get<Deal[]>(url).pipe();
  }

  getDealsAccurately(beginTime: string, endTime: string): Observable<Deal[]> {
    const url = this.dealUrl + '?beginTime=' + beginTime + '&endTime=' + endTime;
    return this.http.get<Deal[]>(url).pipe();
  }
  getAcctDealsAccurately(acctId: number, beginTime: string, endTime: string): Observable<Deal[]> {
    const url = this.dealUrl + '?acctId=' + acctId + '&beginTime=' + beginTime + '&endTime=' + endTime;
    return this.http.get<Deal[]>(url).pipe();
  }

  getAccount(id: number): Observable<Account> {
    const url = this.accountUrl + '?id=' + id;
    return this.http.get<Account>(url).pipe(
      catchError(this.handleError<Account>(`getAccount id=${id}`))
    );
  }
  getAccountByAcct(acct: string): Observable<Account> {
    const url = this.accountUrl + '?acct=' + acct;
    return this.http.get<Account>(url).pipe(
      catchError(this.handleError<Account>(`getAccount id=${acct}`))
    );
  }

  addAccount(account: Account): Observable<Response> {
    return this.http.post<Response>(this.accountUrl, account, httpOptions).pipe(
      catchError(this.handleError<any>('addAccount'))
    );
  }

  updateAccount(account: Account): Observable<Response> {
    return this.http.put<Response>(this.accountUrl, account, httpOptions).pipe(
      catchError(this.handleError<Response>('updateAccount'))
    );
  }

  deleteAccount(account: Account | number): Observable<Response> {
    const id = typeof account === 'number' ? account : account.id;
    return this.http.delete<Response>(this.accountUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteAccount'))
    );
  }

  getAccounts(): Observable<Account[]> {
    return this.http.get<Account[]>(this.accountUrl).pipe(
      catchError(this.handleError<Account[]>('getAccounts', [])));
  }

  getRoles(): Observable<Role[]> {
    return this.http.get<Role[]>(this.roleUrl).pipe(
      catchError(this.handleError<Role[]>('getRoles', [])));
  }

  getCustomers(): Observable<Customer[]> {
    return this.http.get<Customer[]>(this.customerUrl).pipe(
      catchError(this.handleError<Customer[]>('getCustomers', []))
    );
  }

  getCustomer(id: number): Observable<Customer> {
    const url = this.customerUrl + '?id=' + id;
    return this.http.get<Customer>(url).pipe(
      catchError(this.handleError<Customer>(`getCustomer id=${id}`))
    );
  }

  addCustomer(customer: Customer): Observable<Response> {
    return this.http.post<Response>(this.customerUrl, customer, httpOptions).pipe(
      catchError(this.handleError<any>('addCustomer'))
    );
  }

  updateCustomer(customer: Customer): Observable<Response> {
    return this.http.put<Response>(this.customerUrl, customer, httpOptions).pipe(
      catchError(this.handleError<Response>('updateCustomer'))
    );
  }

  deleteCustomer(customer: Customer | number): Observable<Response> {
    const id = typeof customer === 'number' ? customer : customer.id;
    return this.http.delete<Response>(this.customerUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Response>('deleteCustomer'))
    );
  }

  changePw(account: Account): Observable<Response> {
    return this.http.put<Response>(this.changePwUrl, account, httpOptions).pipe(
      catchError(this.handleError<Response>('changePw'))
    );
  }

  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error); // log to console instead
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
